export default (props) => {
  const applicationsCriticalities = [];
  props.points.forEach(point => {
    point.applications.forEach(application => {
      applicationsCriticalities.push(application.criticality);
    });
  });

  const N = applicationsCriticalities.length;

  const criticality = N ? Math.min(...applicationsCriticalities) : null;

  let side = 20 + N*2;
  if (side > 100) side = 100;

  const backgroundColor = criticality !== null ? CRITICALITY[criticality].color : CRITICALITY.default.color;

  // const onClick = (lat, lng) => {
  //
  // };

  return (
    <Container
      className="cluster px-0 justify-content-center align-items-center"
      style={{
        height: side,
        width: side,
        marginTop: -side/2,
        marginLeft: -side/2,
        backgroundColor,
      }}
      onClick={() => props.onClick(props.lat, props.lng, props.points.length > 1)}
    >
      <span className="title">{N}</span>
    </Container>
  );
}
