import requestData from "./core/requestData";
import RequestError from "./core/RequestError";
const LIMIT = 1000;

export default async function getData(path, user) {
  let data = [];
  let offset = 0;
  let dataPortion;

  do {
    const res = await requestData(`${path}${path.includes('?') ? '&' : '?'}limit=${LIMIT}&offset=${offset}`, user);
    if (res.status === 200) { // накапливаем данные
      dataPortion = await res.json();
      data = data.concat(dataPortion);
    } else { // возвращаем ошибку
      return RequestError(res);
    }
    offset+=LIMIT;
  } while (dataPortion.length > 0);

  return data;
}