import NETWORK_PATHS from '../../SETTINGS/NETWORK_PATHS';
const {proxy, homepage, authPath} = NETWORK_PATHS;

const URL = proxy + homepage + authPath;

export default async function(user) {
  let res = await fetch(URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(user),
  });

  return res;
}
