import React, { Component } from 'react';
import Waiter from './Waiter';
import Auth from './Auth';
import Map from './Map';
import Interface from './Interface';

import requestData from '../actions/core/requestData.js';
import convertData from '../actions/convertData.js';
import filterData from '../actions/filterData.js';

import branches from '../testdata/branches.json';
import applications from '../testdata/applications.json';
import getData from "../actions/getData";
import RequestError from "../actions/core/RequestError";

import CRITICALITY from '../SETTINGS/CRITICALITY';

import NETWORK_PATHS from '../SETTINGS/NETWORK_PATHS';
const {branchesPath, applicationsPath} = NETWORK_PATHS;


export default class App extends Component {
  state = {
    authRequired: true,
    error: false,
    waiting: false,
    data: null,
    filteredData: null,
  };

  componentDidMount() {
    // (async () => {
    //   let data = await convertData({branches, applications});
    //   console.log(data);
    //   this.setState({ data, filteredData: data });
    // })();

    this.checkAccess();
  }

  async checkAccess() {
    this.setState({ waiting: true });
    const res = await requestData('');
    if (res.status === 404) {
      this.onAuthSuccess();
    } else if (res.status === 401) {
      this.setState({ waiting: false });
    } else {
      this.onError(res);
    }
  }

  onAuthSuccess(user) {
    this.getData(user);
  }

  async getData(user) {
    user = Object.assign({}, user);
    this.setState({ waiting: true });

    let data = {
      branches: await getData(branchesPath, user),
      applications: await getData(applicationsPath, user),
    };

    for (let key in data) { // обработка ошибок запросов
      if (data[key] instanceof RequestError) {
        this.onError(data[key]);
        return;
      }
    }

    data = await convertData(data, user);

    this.setState({ authRequired: false, data, filteredData: data, waiting: false });
    console.log('data:', this.state.data);
  }

  filter = {criticality: Object.keys(CRITICALITY)};
  search = "";

  filterData = (filter) => {
    this.filter = filter;
    this.setState({
      filteredData: filterData(this.state.data, this.filter, this.search),
    }, () => console.log('filtered data:', this.state.filteredData));
  };

  searchData = (value) => {
    if (!value) value = "";
    this.search = value;
    this.setState({
      filteredData: filterData(this.state.data, this.filter, this.search),
    }, () => console.log('filtered data:', this.state.filteredData));
  };

  exit = () => {
    this.setState({
      authRequired: true,
      data: null,
      filteredData: null,
    });
  };

  onError = (res) => {
    this.setState({ error: res });
  };

  render() {
    const auth = !this.state.waiting && this.state.authRequired ? <Auth key="auth" onAuthSuccess={(user) => this.onAuthSuccess(user)} onError={this.onError}/> : null;
    const waiter = this.state.waiting ? <Waiter key="waiter"/> : null;
    const map = <Map key="map" data={this.state.filteredData}/>;
    const ui = this.state.filteredData ? <Interface key="interface" data={this.state.filteredData} filterData={this.filterData} searchData={this.searchData} exit={this.exit}/> : null;

    let content = [waiter, auth, ui, map];
    if (this.state.error) content = <Err res={this.state.error}/>;

    return content;
  }
}

const Err = (props) => <div>{`${props.res.status} ${props.res.statusText}`}</div>;
