import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import PulseLoader from 'react-spinners/PulseLoader';
import './style.css';

export default () => {
  return (
    <Container fluid className="waiter h-100 w-100 p-0 m-0 text-center">
      <Row className="h-100 w-100 p-0 m-0 align-items-center">
        <Col><PulseLoader color="gray" size={10}/></Col>
      </Row>
    </Container>
  );
}
