import React, {Component} from 'react';
import SearchField from "react-search-field";

class SearchBox extends Component {
  lastValue;

  render() {
    return (
      <SearchField
        placeholder={"Поиск..."}
        onSearchClick={this.props.onSearch}
        onEnter={this.props.onSearch}
      />
    );
  }
}

export default SearchBox;