import React from 'react'
import GoogleMapReact from 'google-map-react';
import Supercluster from 'points-cluster';
import Marker from './Marker';
import Container from 'react-bootstrap/Container';
import './style.css';
import styles from '../../SETTINGS/MAP_COLORS.js';

export default class Map extends React.Component {
  static defaultProps = {
    center: {
      lat: 61.25,
      lng: 73.38
    },
    zoom: 4
  };

  state = {
    clusters: [],
  };

  mapOptions = {
    center: Map.defaultProps.center,
    zoom: Map.defaultProps.zoom,
  };

  static createClusters(mapOptions, data) {
    const clusters = Supercluster(data);
    return clusters(mapOptions).map(({ wx, wy, numPoints, points }) => ({
      lat: wy,
      lng: wx,
      numPoints,
      id: `${numPoints}_${points[0].id}`,
      points,
    }));
  }

  onMapChange = (mapOptions) => {
    this.mapOptions = mapOptions;
    let data = this.props.data ? this.props.data : [];
    const clusters = Map.createClusters(this.mapOptions, data);
    this.setState({ clusters });
  };

  componentDidUpdate(prevProps) {
    // console.log(prevProps.data, this.props.data);
    if (prevProps.data !== this.props.data) this.onMapChange(this.mapOptions);
  }

  handleApiLoaded = (map, maps) => {
    this.internals = {
      map, maps
    };
  };

  onMarkerClick = (lat, lng, zoomNeeded) => {
    const newZoom = this.mapOptions.zoom+1;
    if (zoomNeeded) this.internals.map.setZoom(newZoom);
    this.internals.map.panTo(new this.internals.maps.LatLng(lat, lng));
    this.internals.map.panBy(175, 0);
  };


  render() {
    const clusters = this.state.clusters.map(cluster => {
      return (
        <Marker
          key={cluster.id}
          lat={cluster.lat}
          lng={cluster.lng}
          points={cluster.points}
          onClick={this.onMarkerClick}
        />
      );
    });

    return (
      <Container fluid className="map h-100 w-100 px-0">
        <GoogleMapReact
          bootstrapURLKeys={{
            language: 'ru',
            region: 'ru',
          }}
          defaultCenter={Map.defaultProps.center}
          defaultZoom={Map.defaultProps.zoom}
          onChange={this.onMapChange}
          options={() => ({
              disableDefaultUI: true,
              styles,
          })}
          yesIWantToUseGoogleMapApiInternals
          onGoogleApiLoaded={({ map, maps }) => this.handleApiLoaded(map, maps)}
        >
          {clusters}
        </GoogleMapReact>
      </Container>
    );
  }
}
