import React from 'react';
import './style.css';

import Frame from './Frame';
import BranchList from './BranchList';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

const LIST_WIDTH = 350;
const FRAME_PX = 70;
const FRAME_PY = 30;
const VIEW = {LIST_WIDTH, FRAME_PX, FRAME_PY};

export default class UserInterface extends React.Component {
  state = {
    frameOpen: false,
    recordId: null,
  };

  openFrame = (recordId) => {
    this.setState({frameOpen: true, recordId});
  };

  closeFrame = () => {
    this.setState({frameOpen: false, recordId: null});
  };

  render() {
    //this.props.data.concat(this.props.data).concat(this.props.data).concat(this.props.data).concat(this.props.data).concat(this.props.data).concat(this.props.data)
    const list = <BranchList key="list" {...this.props} data={this.props.data} openFrame={this.openFrame} view={VIEW}/>;

    const content = this.state.frameOpen ?
      (<Container fluid className="Interface h-100 w-100 p-0 m-0">
        <Row className="h-100 w-100 p-0 m-0">
          <Frame key="frame" closeFrame={this.closeFrame} recordId={this.state.recordId} view={VIEW}/>
          {list}
        </Row>
      </Container>)
      :
      (<div className="Interface frame-closed p-0 m-0 h-100">{list}</div>);

    return content;
  }
}
