export default function filterData(data, filter, value) {
  // console.log(data, filter)

  let filteredData = [];

  data.forEach(item => {
    // поиск (если не содержит) - так меньше проходов по массиву чем если отдельно
    if (
      !item.title.toLowerCase().includes(value.toLowerCase())
      &&
      !item.nodeCode.toLowerCase().includes(value.toLowerCase())
    ) return;

    let applications = item.applications.slice();

    // филиалы без заявок
    if (!item.applications.length && filter.criticality && filter.criticality.includes('default')) {
      filteredData.push(Object.assign({}, item));
      return;
    }

    // фильтрация
    for (let key in filter) {
      applications = applications.filter(app => {
        return filter[key].includes(''+app[key]);
      });
    }
    // console.log(item, applications);
    if (applications.length) {
      item = Object.assign({}, item);
      item.applications = applications;
      filteredData.push(item);
    }
  });

  return filteredData;
}
