import NETWORK_PATHS from '../../SETTINGS/NETWORK_PATHS';
const {proxy, homepage, apiPath} = NETWORK_PATHS;
const URL = proxy + homepage + apiPath;


export default async function(path, user) {
  let headers = {
    "Content-Type": "application/json",
  };

  // для работы без куки
  if (user) headers["Authorization"] = "Basic " + btoa(`${user.email}:${user.password}`);

  let res = await fetch(URL + path, {
    method: "GET",
    headers,
  });

  return res;
}
