import React from 'react';
import { Form, Text } from 'informed';
import { Container, Row, Col, Button } from 'react-bootstrap';
import PulseLoader from 'react-spinners/PulseLoader';
import './style.css';

import auth from '../../actions/core/auth.js';
import Image from "react-bootstrap/Image";
import RequestError from "../../actions/core/RequestError";

export default class Auth extends React.Component {
  state = {
    authFailed: false,
    waiting: false,
  };

  async sendAuthRequest(user) {
    this.setState({ waiting: true });
    let res = await auth(user);
    this.setState({ waiting: false });

    console.log(res);

    if (res.status === 200) {
      this.setState({ authFailed: false });
      // this.props.onAuthSuccess();
      this.props.onAuthSuccess(user);
    } else if (res.status === 401) {
      this.setState({ authFailed: true });
    } else {
      // вывести ошибку и выйти
      this.props.onError(new RequestError(res));
    }

  }

  onFormSubmit(user) {
    if (this.state.waiting) return;
    this.sendAuthRequest(user);
  }

  render() {
    return (
      <Container fluid className="Auth h-100 w-100 p-0 m-0">
        <Image height={20} className="logo mt-2 ml-2" src={require("../../images/logo.png")}/>
        <Row className="flex-column h-100 w-100 p-0 m-0 align-items-center justify-content-center">
        <Form
          onSubmit={user => this.onFormSubmit(user)}
          initialValues={{
            // email: "hmrfins@gmail.com",
            // password: "hmrfbfacke",
          }}
        >
          <Col className="px-0">
            <label>
              <Col className="px-0 mb-1">Логин</Col>
              <Col className="email px-0"><Text field="email" type="email"/></Col>
            </label>
          </Col>
          <Col className="px-0">
            <label>
              <Col className="px-0 mb-1">Пароль</Col>
              <Col className="password px-0"><Text field="password" type="password"/></Col>
            </label>
          </Col>
          <Col className="warning px-0">
            {this.state.authFailed ? 'Неверный логин или пароль' : ''}
          </Col>
          <Col className="px-0">
            <Button variant="success" type="submit">
              {this.state.waiting ? <PulseLoader size={7} color={'rgba(255,255,255,0.7)'}/> : 'Войти'}
            </Button>
          </Col>
        </Form>
        </Row>
      </Container>
    );
  }
}
