import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import CRITICALITY from '../../../SETTINGS/CRITICALITY.js';
import './style.css';
import {AutoSizer, List} from "react-virtualized";
import SearchBox from "./SearchBox";
import LinesEllipsis from 'react-lines-ellipsis'

const filterTitles = [];
for (let key in CRITICALITY) {
  filterTitles.push({key, info: CRITICALITY[key], selected: true});
}

export default class BranchList extends React.Component {
  headerHeight = 155;
  itemHeight = 50;
  remainInfoHeight = 25;
  blurerHeight = 12;

  state = {
    filterTitles,
    stopIndex: null,
    startIndex: null,
  };

  changeFilters = () => {
    const filter = {criticality: []};
    this.state.filterTitles.forEach((item) => {
      if (item.selected) filter.criticality.push(item.key);
    });
    this.props.filterData(filter);
  };

  toggleFilterSelect = (index) => {
    const filterTitles = this.state.filterTitles.slice();
    filterTitles[index].selected = !filterTitles[index].selected;
    this.setState({filterTitles});
    this.changeFilters();
  };

  componentDidMount() {
    this.hardcodeBlurBottomCorner();
  }

  hardcodeBlurBottomCorner() {
    const canvas = this.refs.bottomBlurer;
    const ctx = canvas.getContext('2d');
    const linGrad = ctx.createLinearGradient(0, 0, 0, canvas.height);
    linGrad.addColorStop(0, 'rgba(255,255,255,0)');
    linGrad.addColorStop(1, 'rgba(255,255,255,0.8)');
    ctx.fillStyle = linGrad;
    ctx.fillRect(0, 0, this.props.view.LIST_WIDTH, canvas.height);
  }

  render() {
    const filterTitleElements = this.state.filterTitles.map((item, i) => {
      return (
        <FilterTitle
          key={item.info.name}
          title={item.info.name}
          color={item.info.color}
          colorMuted={item.info.colorMuted}
          selected={item.selected}
          index={i}
          toggleFilterSelect={this.toggleFilterSelect}
        />
      );
    });

    const remainCount = this.props.data.length - this.state.stopIndex - 1;


    return (
      <Container className="List h-100 px-0 mx-0" style={{width: this.props.view.LIST_WIDTH}}>
        <Container className="list-header" style={{height: this.headerHeight}}>
          <Row className="px-3 pt-2 pb-2" style={{height: 40}}>
            <Col className="d-flex flex-grow-1 p-0 logo-image align-items-center">
             <img height={22} src={require("../../../images/logo-combined.png")} alt="BPIUM"/>
            </Col>
            <Col className="exit p-0 m-0 flex-grow-0" onClick={this.props.exit}>
              <img height={20} src={require('../../../images/exit.png')}/>
            </Col>
          </Row>
          <Col className="d-flex flex-wrap px-0 mx-0 pb-0 pt-1 align-content-center">
            <SearchBox
              onSearch={this.props.searchData}
            />
          </Col>
          <Col className="titles d-flex flex-wrap px-0 pb-1 pt-1 align-content-center">{filterTitleElements}</Col>
        </Container>
        <AutoSizer>
          {({ height, width }) => (
            <List
              rowHeight={this.itemHeight}
              rowCount={this.props.data.length}
              rowRenderer={({ index, key, style}) => (
                  <div key={key} style={style}>
                    <BranchItem
                      id={this.props.data[index].id}
                      // name={this.props.data[index].title}
                      name={this.props.data[index].address}
                      applications={this.props.data[index].applications}
                      openFrame={this.props.openFrame}
                      height={this.itemHeight}
                    />
                  </div>
              )}
              onRowsRendered={({startIndex, stopIndex}) => this.setState({startIndex, stopIndex})}
              height={height-this.headerHeight-(remainCount > 0 ? this.remainInfoHeight : 0)}
              width={width}
            />
          )}
        </AutoSizer>
        <canvas
          className={'blur-bottom-corner'}
          ref={'bottomBlurer'}
          hidden={remainCount<=0}
          height={this.blurerHeight}
          width={this.props.view.LIST_WIDTH-5}
          style={{bottom: this.remainInfoHeight-1}}
        >

        </canvas>
        <RemainInfo count={remainCount} height={this.remainInfoHeight}/>
      </Container>
    );
  }
}

const RemainInfo = (props) => {
  return props.count > 0 ? (<Col className="remain-info w-100 px-3 m-0" style={{height: props.height}}>
    {`и еще ${props.count} объектов...`}
  </Col>) : null;
};

const FilterTitle = (props) => {
  return (
    <Col
      onMouseDown={(e) => e.preventDefault()}
      onDoubleClick={(e) => e.preventDefault()}
      onClick={() => {
        props.toggleFilterSelect(props.index);
      }}
      className="d-flex flex-wrap flex-grow-0 px-0 text-center"
    >
      <div style={{backgroundColor: props.selected ? props.colorMuted : null, color: props.color}} className="filter-title">{props.title}</div>
    </Col>
  );
};

const BranchItem = (props) => {
  const filters = {};
  props.applications.forEach((a) => {
    filters[a.criticality] = filters[a.criticality] ? filters[a.criticality]++ : 1;
  });

  const filterItems = [];
  for (let key in filters) {
    filterItems.push(<FilterItem key={key} count={filters[key]} colorMuted={CRITICALITY[key].colorMuted} color={CRITICALITY[key].color}/>);
  }

  return (
    <Container className="p-0 mx-0">
      <Row
        className="list-item px-3 m-0 align-items-center"
        style={{height: props.height}}
        onClick={() => props.openFrame(props.id)}
      >
        <Col
          title={props.name}
          className="flex-grow-1 name px-0 mx-0"
          style={{maxHeight: props.height}}
        >
          <LinesEllipsis
            text={props.name}
            maxLine='2'
            ellipsis='...'
            trimRight
            basedOn='letters'
          />
        </Col>
        {filterItems}
      </Row>
    </Container>
  );
};

const FilterItem = (props) => {
  return (
    <Col className="flex-grow-0 filter-item px-0 text-center" style={{backgroundColor: props.colorMuted, color: props.color}}>{props.count}</Col>
  );
};
