import changeRecord from './core/changeRecord';
import NETWORK_PATHS from '../SETTINGS/NETWORK_PATHS';
const {branchesPath} = NETWORK_PATHS;
const COORDS_FIELD = '20';

const maps = window.google.maps;
const geocoder = new maps.Geocoder();

export default async function (data, user) {
  console.log(data);
  const branches = {};
  const requests = [];
  data.branches.forEach((branch) => {
    const id = branch.id;
    const title = branch.title;
    const nodeCode = branch.values[30];
    const address = branch.values[6];
    const coords = branch.values[20];
    const applications = [];

    if (coords) { // уже геокодили точку
      const [lat, lng] = coords.split(',').map(a => +a);
      branches[id] = {title, address, lat, lng, applications, nodeCode};
      return;
    }

    requests.push(new Promise((resolve, reject) => { // еще нет, геокодим и записываем координаты
      geocoder.geocode({'address': address, 'region': 'ru'}, async (results, status) => {
        if (status === 'OK') {
          const lat = results[0].geometry.location.lat();
          const lng = results[0].geometry.location.lng();
          const type = results[0].geometry.location_type;
          if (type === 'ROOFTOP' || type === 'RANGE_INTERPOLATED') {
            // успешный геокодинг
            branches[id] = {title, address, lat, lng, applications, nodeCode};
            const res = await changeRecord(`${branchesPath}/${id}`, COORDS_FIELD, `${lat},${lng}`, user);
            console.log(address, ' coords writing: ', res.status);
          } else {
            // не успешный
            console.log('Incorrect address ' + address + ' skipped, id ', id);
          }
        } else {
          console.log('Error: ', address, status);
        }
        resolve();
      });
    }));
  });

  await Promise.all(requests);

  data.applications.forEach(item => {
    const branchId = item.axis.recordId;
    const applications = item.values.map(item => ({
      criticality: +item.split,
    }));
    if (branches[branchId]) branches[branchId].applications = applications;
  });

  const points = [];
  for (let key in branches) {
    branches[key].id = key;
    points.push(branches[key]);
  }

  return points;
}
