import React from 'react';
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import './style.css';

class Frame extends React.Component {
  render() {
    return (
      <Col
        className="Frame m-0"
        style={{
          paddingTop: this.props.view.FRAME_PY,
          paddingBottom: this.props.view.FRAME_PY,
          paddingRight: this.props.view.FRAME_PX,
          paddingLeft: this.props.view.FRAME_PX
        }}>
        <Container fluid className="p-0 m-0 h-100 w-100">
          <div className={'iframe-container'}>
            <div onClick={this.props.closeFrame} className={'close-button'}>✕</div>
            <iframe
              width='100%'
              height='100%'
              src={`https://megapolis.bpium.ru/?action=record-open&catalog=12&record=${this.props.recordId}&screen=full`}
            />
          </div>
        </Container>
      </Col>
    );
  }
}

export default Frame;
